<template>
  <el-container>
    <el-header>
      <!--  <header-buttons
          @buttonClickEvent="buttonClickEvent"
          cssType="100"
          showBack=true
          showTitle=true
          :titleName="CONSTANTS_DEVICE_CONFIGURATION.ADD_DEVICE"
          >
          </header-buttons> -->
      <HeaderTitle class="header-title" :titleName="CONSTANTS_DEVICE_CONFIGURATION.ADD_DEVICE">
        <div slot="head-operation" class="head-operation">
          <!-- <el-button type="default" size="mini" @click="insertEvent">{{ BTN_TEXT.INSERT_ONE_ROW }}</el-button> -->
          <HeadOperationItem name="addDevice" @onOprationItem="exportData">
            <i slot="operation-icon" class="icon-export"></i>
            <span slot="operation-name">{{ BTN_TEXT.EXPORT }}</span>
          </HeadOperationItem>
          <HeadOperationItem name="addDevice" @onOprationItem="importTable">
            <i slot="operation-icon" class="icon-import"></i>
            <span slot="operation-name">{{ BTN_TEXT.IMPORT }}</span>
          </HeadOperationItem>
        </div>
      </HeaderTitle>
    </el-header>
    <el-main>
      <div class="area-main">
        <vxe-grid border="full" resizable keep-source ref="xGrid" class="vxe-table-element" height="550"
          :edit-rules="validRules" :columns="tableColumns" :data="tableDatas"
          :edit-config="{ trigger: 'click', mode: 'row', showStatus: true, icon: 'fa fa-file-text-o' }">
        </vxe-grid>
        <div class="btnDiv">
          <el-button type="primary" @click="fullValidEvent">{{ BTN_TEXT.SAVE }}</el-button>
          <el-button @click="goBack">{{ BTN_TEXT.RESET }}</el-button>
        </div>
      </div>
    </el-main>
    <!-- 导入模态框 -->
    <ImportDialog ref="ImportDialog" v-if="isImportDialogShow" :importDialogName="EXPORT_NAME.DEVICE_TITLE"
      :exportFileName="EXPORT_NAME.DEVICE_TEMPLATE" @exportTemplate="exportTemplate" @submitImportFile="submitImportFile"
      @closeImportDialog="closeImportDialog"></ImportDialog>
  </el-container>
</template>

<script>
import js from '@/assets/js/company/DeviceManange/addDevice';
export default js;
</script>

<style lang="less" scoped>
@import '../../../assets/less/company/DeviceManange/addDevice.less';
</style>
